import React, { useEffect } from 'react';
import PubSub from 'pubsub-js';
import LocalStorageService from '../service/LocalStorageService';
import { updateLicence , submitToken, getProjectLicence } from '../service/APIRunner';
import Dropdown from 'react-dropdown';
import 'react-dropdown/style.css';


const licenceStatus = [
  {
    value: "0",
    label: "Inactive(Licence Expired or Cancelled)"
  },
  {
    value: "1",
    label: "Active"
  },
  {
    value: "2",
    label: "Warning(Licence in Grace Period)"
  },
  {
    value: "3",
    label: "Suspended(Licence is Suspended)"
  },
  {
    value: null,
    label: "Delete"
  }
];

export function APIForm() {
  const [licence, setLicence] = React.useState(null);
  const [project, setProject] = React.useState(null);
  const [apiResponse, setApiResponse] = React.useState('');
  const [token, setToken] = React.useState(LocalStorageService.getItem('token') || '');
  const [tokenStatus, setTokenStatus] = React.useState(LocalStorageService.getItem('token') ? true : false);

  useEffect(() => {
    PubSub.subscribe('UPDATE_RESPONSE', (_, response) => {
      if (
        response.status === 'success') {
        setApiResponse('Licence update success');
      } else {
        setApiResponse('Error, something went wrong');
      }
    })
    PubSub.subscribe('LICENCE_RESPONSE', (_, response) => {
      if (response) {
        setLicence(response);
      } else {
        setApiResponse('Error, something went wrong');
      }
    })
    return () => {
      PubSub.unsubscribe('UPDATE_RESPONSE');
      PubSub.unsubscribe('LICENCE_RESPONSE');
    };
  });

  useEffect(() => {
    if(project){
      getProjectLicence(project);
    }
  }, [project]);

  const updateCurrentLicence = (value, index) => {
    licence[index].spIdentifier = value;
    setLicence(licence.slice());
  }

  const updateLicenceState = (value, index) => {
    if(value === null){
      licence.splice(index, 1);
      setLicence(licence.slice());
      return;
    }
    licence[index].state = parseInt(value);
    setLicence(licence.slice());
  }
  const currentProjectPlans = window.projectPlans?.[project] || [];
  const addLicence = () => {
    licence.push({ spIdentifier: currentProjectPlans[0].value, state: 1 });
    console.log(licence);
    setLicence(licence.slice());
  }


  const handleSubmit = async (e) => {
    e.preventDefault();
    setApiResponse('Sending request...');
    updateLicence({licence, project});
  }

  if (!tokenStatus) {
    return <div className="form-group">
      <div className="group-header">Token</div>
      <input type="text"
        className="form-control"
        id="service-url"
        value={token}
        onKeyDown={(e) => {
          if (e.key === 'Enter') {
            setTokenStatus(submitToken(token));
          }
        }}
        onChange={(event) => setToken(event.target.value)} />
    </div>
  }

  if (apiResponse) {
    return (
      <div className='build-status'>
        <div className={`message build-status--success`}>
          {apiResponse}
        </div>
      </div>
    )
  }

  const projects = window.projects || [];
  console.log({projects, currentProjectPlans});

 
  return (
    <form id="ci-form" onSubmit={handleSubmit}>
      <div className="form-group">
            <div className="group-header">Project</div>
            <Dropdown
              options={projects  || []}
              value={project}
              onChange={(e) => setProject(e.value)}
              placeholder="Select a project"
            />
          </div>
      <div className="licence-list">
      {licence ? licence.map((item, index) =>
        <div className='licence-group'>
          <div className="form-group" key={`${index}-licence`}>
            <div className="group-header">Licence</div>
            <Dropdown
              options={currentProjectPlans}
              value={item.spIdentifier}
              onChange={(e) => updateCurrentLicence(e.value, index)}
              placeholder="Select an option"
            />
          </div>
          <div className="form-group" key={`${index}-status`}>
            <div className="group-header">Status</div>
            <Dropdown
              options={licenceStatus}
              value={item.state.toString()}
              onChange={(e) => updateLicenceState(e.value, index)}
              placeholder="Select an option"
            />
          </div>
        </div>) : project && <div className='build-status'>
        <div className={`message build-status--success`} style={{'marginTop':'20px'}}>
          Loading current licence...
        </div>
      </div>}
      </div>
      {project && licence && <div className="form-group">
        <button type="button" className="btn btn-primary" id="add-btn" onClick={addLicence}>Add Licence</button>
        <button type="submit" className="btn btn-primary" id="submit-btn">Submit</button>
      </div>}
    </form>
  );
}
