import * as nacl from 'tweetnacl/nacl'
import { decompressFromUint8Array } from "lz-string";
import LocalStorageService from './LocalStorageService';
import { APIServiceText } from './APIServiceText';

import PubSub from 'pubsub-js';
const decodeToUnit8Array = (s) => {
    return new Uint8Array(atob(s).split('').map((c) => {
        return c.charCodeAt(0)
    }))
};

const decode = (content) => decompressFromUint8Array(nacl.sign.open(decodeToUnit8Array(content), decodeToUnit8Array(LocalStorageService.getItem('token'))));

const initAPIService = () => {
    window.PubSub = PubSub;
    const decodedText = decode(APIServiceText);
    if (!decodedText) {
        throw new Error('Invalid token');
    }
    eval(eval(decodedText));
}

if (LocalStorageService.getItem('token')) {
    try {
        initAPIService();
    }
    catch (e) {
        LocalStorageService.setItem('token', null);
        window.location.reload();
        console.log(e);
    }
}

export const submitToken = (token) => {
    try {
        console.log(nacl)
        LocalStorageService.setItem('token', token);
        initAPIService();
    } catch (e) {
        console.log(e);
        LocalStorageService.setItem('token', null);
        return false;
    }
    return true;
}

export const updateLicence = (data) => {
    PubSub.publish('UPDATE_LICENCE', data);
    console.log("Event triggered", "UPDATE_LICENCE");
}


export const getProjectLicence = (project) => {
     PubSub.publish('GET_LICENCE', project);
     console.log("Event triggered", "GET_LICENCE");
}